@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');
body{
  margin:0;
  font-family: 'Quicksand';
  color: #ddd;
  background-image:linear-gradient(#8b9da9,#8b9da9);
}
/* top bar */
.top{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}
.top input[type=text]{
  margin-left: auto;
  border: 1px solid #ddd;
  box-sizing: border-box;
  border-radius: 8px;
  width: 100px;
  transition: width 0.5s ease-in-out;
}
.top input[type=text]:hover{
  width: 30%;
}
.top input[type=text]:focus{
  width: 30%;
}
.top h1{
  font-weight: bold;
}
/* -- */
.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid grey;

}
.navbar h1{
  color: #f1356d;

}
.navbar .links{

  margin-left: auto;

}
.navbar a{
  margin-left: 16px;
  padding: 6px;
  text-decoration: none;
  color: #ddd;
  
}
.navbar a:hover{
  color: #f1356d;

}
.content{
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}
/* Base Styles */
.blog-preview{
  padding: 10px 16px;
  margin: 20px 0;
  /* border-bottom: 1px solid whitesmoke; */
}
.blog-preview:hover{
  /* box-shadow: 1px 3px 5px rgb(0, 0, 0,0.1); */
  border-left:4px solid #f1356d;
  
}
.blog-preview h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.blog-preview a{
  text-decoration: none;
  color: #ddd;
}
.blog-details h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 1opx;
}

.blog-details div{
  margin: 20px 0;
}
.blog-details button{
  background: #f1356d;
  color: #fff;
  border: 0px;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;

}
/* form style */

.create{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.create label{
  display: block;
  text-align: left;
}

.create h2{
  color: #f1356d;
  font-size: 20px;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select{
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
  border-radius: 15px;
  
}
.create button{
  background-color: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 13px;
  cursor: pointer;
}

